<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-card class="ma-1" cols="12">
        <v-container>
        <v-row>
            <v-col cols="12">
            <v-select
            class="mb-n5"
            :items="stores"
            label="*Store owner"
            v-model="referral.store_owner"
            outlined
            dense
            ></v-select>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            label="*Name"
            required
            v-model="referral.name"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            label="*Destination email"
            required
            v-model="referral.destination_email"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            label="*Phone number"
            required
            v-model="referral.phone_number"
            ></v-text-field>
            </v-col>
            <v-col>
            <router-link to="/referrals">
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            >
            GO BACK
            </v-btn>
            </router-link>
            <v-btn
            class="mx-4 my-2"
            color="#E47911"
            dark
            :loading="loading"
            @click= "update"
            >
            SAVE
            </v-btn>
            </v-col>
        </v-row>
        </v-container>
        </v-card>
    </div>
</template>

<script>
    import NavBar from '@/components/NavBar';
    import ReferralService from '@/services/ReferralService';
    import Swal from 'sweetalert2'

    export default {
        components: {
        NavBar
        },

        data: () => ({
            title: 'UPDATE REFERRAL',
            loading: false,
            referral: {
                store_owner: "",
                name: "",
                destination_email: "",
                phone_number: "",
            },
            stores: [],
        }),
        
        computed:{
            unique_id(){
                return this.$route.params.id
            }
        },

        mounted(){
            ReferralService
                .get(this.unique_id)
                .then(res => {
                    console.log(res)
                    this.referral.store_owner = parseInt(res.store_owner);
                    this.referral.name = res.name;
                    this.referral.destination_email = res.destination_email;
                    this.referral.phone_number = res.phone_number;
                })
            
            ReferralService
            .list_owners()
            .then(res => {
                for (let i = 0; i < res.length; i++) {
                    this.stores.push({ text: res[i].store.name + ' - ' + res[i].owner.username, value: res[i].id });
                }
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */

                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });

            })
        },

        methods: {
            update(){
                ReferralService
                    .update(this.referral, this.unique_id)
                    .then(res => {
                        Swal.fire({
                        icon: 'success',
                        title: 'Success',
                        text: 'The referred was successfully modified.'
                        }).then(() => {
                            this.$router.push('/referrals')
                        })
                    })
            }
        }
    }
</script>